.profile__wrapper{display:flex;margin:0;padding:3rem;border-bottom:1px solid var(--color-grey)}.profile__left{display:flex;flex:2;flex-direction:column;align-items:center}.profile__right{flex:6}.profile__img{width:7.14rem;height:7.14rem;margin:0 auto 1rem;object-fit:cover;border-radius:100%}.profile__load{width:8rem;margin-bottom:1rem;color:rgba(0,0,0,0)}.profile__load::-webkit-file-upload-button{visibility:hidden}.profile__load::before{display:inline-block;color:var(--primary-dark);font-weight:500;font-size:1rem;white-space:nowrap;outline:none;cursor:pointer;content:"Загрузить аватар ⭳";user-select:none}.profile__load:hover::before{color:var(--primary)}.profile__load:active{outline:0}.profile__load:disabled::before{color:var(--color-grey-dark)}.profile__name,.profile__email,.profile__password-label{margin-bottom:1rem;font-weight:500;font-size:1.2rem}.profile__error{color:var(--primary);font-weight:500;font-size:1rem;text-align:center}.profile .spinner{width:7.14rem;height:7.14rem}.achivements{padding:3rem}.achivements__time{display:flex;flex-direction:column;align-items:center;justify-content:center}.achivements__img{width:7.14rem}.achivements__desc{margin-top:1rem;font-weight:500;font-size:1.2rem;text-align:center}.app.dark .profile__load::before{color:var(--primary-light)}.app.dark .profile__load:hover::before{color:var(--primary)}.app.dark .profile__load:disabled::before{color:var(--color-grey-dark)}